.footer {
  &__content {
    display: flex;
    justify-content: center;
    margin-top: 64px;
    width: 100%;
    height: 206px;
    background-image: url("/images/bgfooter.jpg");
    background-position: 50%;
    background-repeat: no-repeat;
    background-size: cover;

    @include on-desktop() {
      height: 359px;
    }
  }

  &__logo {
    margin-bottom: 25px;
    align-self: end;

    @include on-desktop {
      margin-bottom: 35px;
    }
  }
}
